import React from "react"
import Link from "gatsby-link"


export default ({ data }) => 
<div>
    <div className="jumbotron">
        <div className="container">
            <h1>Welcome to Return of the Shadow!</h1>
            <p>Return of the Shadow is a game based on the Fourth Age of Arda, J.R.R. Tolkien's wonderful world of fantasy. Game play takes place after the destruction of the One Ring. After Sauron's fall, a period of peace was laid about the land, and the Elves, Humans, Dwarves, and Hobbits prospered.</p>
            <p><Link className="btn btn-primary btn-lg" to="/connect" role="button">Start Playing &raquo;</Link></p>
        </div>
    </div>
    <section className="news-preview">
        <div className="container">
            <div className="row">
                <h1 className="title">News</h1>
            </div>
            {data.allMarkdownRemark.edges.map(({ node }) =>
                <div key={node.id} className="col-md-4">
                    <h2>{node.frontmatter.title}</h2>
                    <p>{node.excerpt}</p>
                    <p><Link className="btn btn-default" to={node.frontmatter.path} role="button">View Details &raquo;</Link></p>
                </div>
            )}
        </div>
    </section>
    <section className="races offwhite">
        <div className="container">
            <div className="row">
                <h1 className="title">Races</h1>
            </div>
            <div className="row">
                <h2>Good Races</h2>
            </div>
            <div className="row">
                <h2>Evil Races</h2>
            </div>
        </div>
    </section>
    <section className="classes">
        <div className="container">
            <div className="row">
                <h1 className="title">Classes</h1>
            </div>
        </div>
    </section>
</div>


export const query = graphql `
query IndexQuery {
    allMarkdownRemark(limit: 6, sort: {fields: [frontmatter___date], order: DESC}) {
        edges {
            node {
                id
                frontmatter {
                    title
                    date(formatString: "DD MMMM, YYYY")
                    path
                }
                excerpt
            }
        }
    }
}
`


